// import { isArray } from 'util';

export default {
    data() {
        return {
            loading: true,
            list: [],
            pagination: {
                total: 0,
                currentPage: 1
            },
            page: 1,
            keyword: ''
        }
    },

    mounted() {
        this.refresh()
    },

    methods: {
        handleCurrentChange(val) {
            this.page = val
            this.refresh()
        },
        onKeywordChange() {
            this.page = 1
            this.refresh()
        },
        refresh() {
            this.loading = true
            this.$store.dispatch('orderList', {
                data: {
                    page: this.page,
                    keyword: this.keyword
                },
                success: data => {
                    const list = data.data.map(it => {
                        return it
                    })

                    this.list = list
                    this.pagination.currentPage = data.current_page
                    this.pagination.total = data.total
                    this.pagination.pageSize = data.per_page
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        },

        onAddClick() {
            this.$refs['addDialog'].showDialog({
                onSuccess: () => {
                    this.refresh()
                }
            })
        },

        remove(id) {
            this.$store.dispatch('carDelete', {
                data: {id},
                success: () => {
                    this.refresh()
                },
                error: msg => {
                    this.alert(msg)
                }
            })
        },

        onEdit(car) {
            this.$refs['addDialog'].showDialog({
                car,
                onSuccess: () => {
                    this.refresh()
                }
            })
        },

        onDelete(car) {
            this.confirm(`确认要删除[${car.model}]吗？`, () => {
                this.remove(car.id)
            })
        }
    }
}