<style lang="less" src="./list.less"></style>

<template>
    <el-dialog :title="edit ? '编辑金融方案' : '添加金融方案'" :visible="open" width="500px" :before-close="onClose">
        <el-form  ref="form" :model="form" label-width="90px" size="small">
            <el-form-item label="方案名称" prop="name">
                <el-input v-model="form.mode" @change="refreshPinyin"></el-input>
            </el-form-item>
            <el-form-item label="描述" prop="description">
                <el-input v-model="form.description"></el-input>
            </el-form-item>
            <el-form-item label="类型" prop="type" class="paymode-radio">
                <el-radio-group v-model="form.type">
                    <el-radio :label="0">全款</el-radio>
                    <el-radio :label="1">贷款</el-radio>
                </el-radio-group>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="open = false">取 消</el-button>
            <el-button type="primary" @click="onSubmit">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import _ from 'lodash'

    export default {

        data() {
            return {
                open: false,
                edit: false,
                form: {
                    id: '',
                    mode: '',
                    description: '',
                    type: 0
                },
                callback: null
            }
        },

        mounted() {
            require.ensure([], () => {
                this.$pinyin = require('../../../lib/pinyin')
            }, 'pinyin')
        },

        methods: {
            showDialog({onSuccess, paymode}) {
                this.open = true
                this.edit = !!paymode
                if (paymode) {
                    this.form = _.pick(paymode, ['id', 'mode', 'description', 'type'])
                } else {
                    this.form = {id: '', mode: '', description: '', type: 0}
                }
                this.callback = onSuccess
            },

            onClose() {
                this.open = false
            },

            refreshPinyin() {
                this.form.pinyin = this.$pinyin.toPinYin(this.form.name)
            },

            handleAvatarSuccess(data) {
                this.form.logo = _.get(data, 'data.url', '')
            },

            onSubmit() {
                this.$refs['form'].validate(valid => {
                    if (valid) {
                        this.$store.dispatch(this.edit ? 'paymodeEdit' : 'paymodeAdd', {
                            data: this.form,
                            success: () => {
                                if (_.isFunction(this.callback)) {
                                    this.open = false
                                    this.callback()
                                }
                            },
                            error: msg => {
                                this.alert(msg)
                            }
                        })
                    }
                })
            }
        }
    }
</script>