import pListAddDialog from './list-add-dialog'

export default {
    data() {
        return {
            loading: true,
            list: [],
            paymodeList: ['全款', '贷款']
        }
    },

    components: {
        pListAddDialog
    },

    mounted() {
        this.refresh()
    },

    methods: {
        refresh() {
            this.loading = true
            this.$store.dispatch('paymodeList', {
                success: data => {
                    this.list = data.map(it => {
                        const type = parseInt(it.type) ? 1 : 0
                        it.type_desc = this.paymodeList[type]
                        return it
                    })
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        },

        onAddClick() {
            this.$refs['addDialog'].showDialog({
                onSuccess: () => {
                    this.refresh()
                }
            })
        },

        remove(id) {
            this.$store.dispatch('paymodeDelete', {
                data: {id},
                success: () => {
                    this.refresh()
                },
                error: msg => {
                    this.alert(msg)
                }
            })
        },

        onEdit(paymode) {
            this.$refs['addDialog'].showDialog({
                paymode,
                onSuccess: () => {
                    this.refresh()
                }
            })
        },

        onDelete(paymode) {
            this.confirm(`确认要删除[${paymode.mode}]吗？`, () => {
                this.remove(paymode.id)
            })
        }
    }
}